<template>
  <div>
        <b-button variant="warning" v-b-modal.modalPopover>Resumen por Entregas</b-button>

        <b-modal id="modalPopover" title="Resumen" ok-only>
        <div class="row">
            <div class="col-md-12">
                <b-table striped hover :fields="fields" :items="items">

                    <template #cell(nro)="data">
                        {{ data.index + 1 }}
                    </template>

                    <template #table-caption>Total Entregados: {{ total }}</template>
                </b-table>
            </div>
        </div>
        </b-modal>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    name: 'Modal',
    data() {
        return {
            fields: [
                'nro',
                { key: 'name', label: 'Lugar' },
                { key: 'num', label: 'Cantidad' },
            ],
            items: [],
            total: 0,
        }
    },
    methods: {
        getResumen(){
            const path = `${process.env.VUE_APP_BASE_URL}resume/`
            axios.get(path)
            .then(res => {
                this.items = res.data
                this.items.forEach(element => {
                    this.total = this.total + element.num
                });
            })
            .catch(err => {
                console.error(err); 
            })
        }
    },
    created() {
        this.getResumen()
    },

}
</script>

<style>

</style>


