<template>
  <div class="">
    <div class="overflow-auto">
      <div class="mt-3 pagination justify-content-center">
        <b-pagination
          v-model="currentPage"
          :total-rows="rows"
          :per-page="perPage"
          first-number
          last-number        
        ></b-pagination>
      </div>

    </div>
    
  </div>
</template>

<script>

export default {
    name: 'Pagination',
    components:{
      
    },
    props: {
        rows : Number,
    },
    data() {
        return {
            // rows: 100,
            perPage: 1,
            currentPage: 5
        };
    },
    watch: {
      'currentPage': function() {   
        // console.log(this.currentPage)
        this.refreshJSON(this.currentPage)
      }
    },
    methods: {
        refreshJSON(page){
            // console.log(`Pagina ${page}`)
            this.$emit('messageFromChild', page)
        }
    },
    created() {
        // console.log(this.rows)
    },
};
</script>

<style lang="css" scoped>

</style>


