<template>
  <div class="">
    <b-input-group prepend="Beneficiario" class="mt-3">
      <b-form-input v-model="search" placeholder="Encontrar..." autofocus
        @keyup.enter="refreshSearch"></b-form-input>
      <b-input-group-append>
        <b-button variant="success" @click="refreshSearch">
          <b-icon icon="search"></b-icon>
        </b-button>
        
      </b-input-group-append>
    </b-input-group>
    
  </div>
</template>

<script>

export default {
    name: 'Search',
    components:{
      
    },
    props: {
        rows : Number,
    },
    data() {
        return {
            search: ''
        };
    },
    watch: {
    },
    methods: {
        refreshSearch(){
            if (this.search != ''){
              this.$emit('messageFromChild', this.search)
            }else{
              // this.search = ' '
              this.$emit('messageFromChild', ' ')
            }
        }
    },
    created() {
        // console.log(this.rows)
    },
};
</script>

<style lang="css" scoped>

</style>


