<template>
    <div>
        <div class="row">
            <div class="col-md-12">                            
                <b-card >
                    <b-card-text>
                        <div class="text-center">                            
                            <h4>Buscador de RUCs:</h4>
                            <hr>
                        </div>
                        <div class="container px-4">
                            <form @submit="onSubmit">
                                <div class="row mb-2">
                                    <div class="col-md-4 mb-3">
                                        <div class="input-group">
                                            <label class="input-group-text" for="inputGroupSelect01">RUC</label>
                                            <input type="number" class="col-md-6 form-control" 
                                            v-model="ruc" id="ruc" 
                                            :class=" {'is-invalid': errores.ruc} "
                                            :disabled="success" 
                                            />
                                            <span class="input-group-addon input-group-addon-btn bg-white">
                                                <div class="" v-if="!success">
                                                    <button class="btn px-2 btn-primary" type="submit"
                                                    :disabled="isDisabled">
                                                        <b-icon-search></b-icon-search> 
                                                    </button>
                                                </div>
                                                <div class="" v-else>
                                                    <button class="btn px-2 btn-danger" @click.prevent="clearForm">
                                                        <b-icon-basket></b-icon-basket> 
                                                    </button>
                                                </div>

                                            </span>
                                        </div>
                                        <b-form-text id="password-help-block">
                                            <div class="" v-if="errores.ruc">
                                                <small class="text-danger">{{ errores.ruc[0] }}</small>
                                            </div>
                                            <div class="" v-else>
                                                Solo estan permitido valores [0-9].

                                            </div>
                                        </b-form-text>
                                    </div>
                                    
                                    <div class="col-md-4 mx-1">
                                        <div class="row">
                                            <label for="inputEmail3" class="col-md-4 col-md-form-label">Tipo</label>
                                            <div class="col-md-8">
                                            <input type="text" class="form-control" :value="rpta.tipo" disabled>
                                            </div>

                                        </div>
                                    </div>
                                    <div class="col-md-2" v-if="success">
                                        <button class="btn btn-primary" @click.prevent="sendForm" :disabled="!this.new">
                                            <b-icon-chevron-double-right></b-icon-chevron-double-right> 
                                        </button>
                                        <button class="btn btn-secondary">
                                            <b-icon-arrow-return-left></b-icon-arrow-return-left>
                                        </button>
                                    </div>

                                    <div class="row mb-3">
                                        <label for="razon_social" class="col-md-2 col-md-form-label">Razon social</label>
                                        <div class="col-md-10">
                                        <input type="text" class="form-control" disabled v-model="form.razon_social">
                                        </div>
                                    </div>
                                    <div class="row mb-3">

                                        <div class="col-md-6">
                                            <div class="row">
                                                <label for="inputEmail3" class="col-md-4 col-md-form-label">Ap. Paterno</label>
                                                <div class="col-md-8">
                                                <input type="text" class="form-control" disabled v-model="form.last_name_p">
                                                </div>
                                            </div>
                                        </div>

                                        <div class="col-md-6">
                                            <div class="row">
                                                <label for="inputEmail3" class="col-md-4 col-md-form-label">Ap. Materno</label>
                                                <div class="col-md-8">
                                                <input type="text" class="form-control" disabled v-model="form.last_name_m" >
                                                </div>
                                            </div>
                                        </div>

                                    </div>
                                </div>
                            </form>

                        </div>
                    </b-card-text>
                </b-card>
            </div>
        </div>

    </div>
</template>

<script>
// import swal from 'sweetalert'
import axios from 'axios'
axios.defaults.xsrfHeaderName = "X-CSRFTOKEN"
axios.defaults.xsrfCookieName = "csrftoken"

export default {
    name: 'SearchRUC',
    data() {
        return {
            ruc: '',
            success: false,
            new: false,
            form: {
                ruc: '',
                razon_social: '',
                last_name_p: '',
                last_name_m: ''
            },
            rpta: [],
            errores: {},
            // 
            server: process.env.VUE_APP_SERVER,
            error: false,
        }
    },
    computed: {
        isDisabled() {
            if(this.ruc.indexOf('.') != -1){
                return true
            }else{
                if(this.ruc.length === 11){
                    return false
                }else{
                    return true
                }
            }
        },
    },
    methods: {
        clearForm() {
            
            this.errores = {}
            this.new = false
            this.success = false
            this.rpta = []
            // LImpiando el Formulario
            this.ruc = ''
            this.form.ruc = ''
            this.form.razon_social = ''
            this.form.last_name_p = ''
            this.form.last_name_m = ''
        },
        onSubmit(evt) {
            evt.preventDefault()
            this.searchLocal(this.ruc)
        },
        searchLocal(ruc){
            const path = `${process.env.VUE_APP_BASE_URL}natural/${ruc}/search/`

            axios.get(path)
            .then(res => {
                if (res.data.message){
                    console.log(res.data.message)
                    console.log("BUScando")
                    // Remoteamente
                    this.searchRemote(this.ruc)
                }else{
                    // Si existe
                    this.errores.ruc = ["ruc ya registrado en BD"]
                    this.success = true
                    this.rpta = res.data[0]
                    this.form.razon_social = this.rpta.razon_social
                    this.form.last_name_p = this.rpta.last_name_p
                    this.form.last_name_m = this.rpta.last_name_m
                }
            })
            .catch(err => {
                if(err.response ){
                    console.log(err.response)
                }
            })
        },
        searchRemote(ruc){
            axios.get(`https://ruc.optimizeperu.com/api/company/${ruc}`)
            // https://dni.optimizeperu.com/api/company/ruc
            .then(res => {
                // Rpta Valida
                if(res.data === 'Peticiones diarias excedidas'){
                    alert(res.data)
                }else{
                    if(res.data === {}){
                        this.errores.ruc = ['Ningun Dato Encontrado, Revise']
                    }else{
                        this.rpta =  res.data
                        this.error = false
                        this.new = true
                        this.success = true
        
                        this.form.ruc = this.ruc
                        this.form.razon_social = this.rpta.name
                        this.form.last_name_p = this.rpta.first_name
                        this.form.last_name_m = this.rpta.last_name
                    }
                }
            })
            .catch(err => {
                console.error(err); 
            })
        },
        sendForm(){
            if(this.success===true && this.new == true){
                const path = `${process.env.VUE_APP_BASE_URL}natural/`

                axios.post(path, this.form)
                .then(res => {
                    console.log(res)
                    swal("Guardado correctamente", "", "success")
                    this.clearForm()
                })
                .catch(err => {
                    if( err.response ){
                        this.errores = err.response.data
                        console.log(this.errores)
                    }
                })
            }
        },
        loadData(data){

        }
              
    },

}
</script>

<style>

</style>


