<template>
    <div class="">
        <div class="row">
            <div class="col text-center">
                <h2>Consults</h2>
                <b-form-group
                label="Button style radios with outline-primary variant and size lg"
                v-slot="{ ariaDescribedby }"
                >
                    <b-form-radio-group
                        id="btn-radios-2"
                        v-model="selected"
                        :checked="selected"
                        :options="options"
                        :aria-describedby="ariaDescribedby"
                        button-variant="outline-primary"
                        size="sm"
                        name="radio-btn-outline"
                        buttons
                    ></b-form-radio-group>
                </b-form-group>
            </div>
            <div class="col-md-12">
                <b-tabs content-class="mt-3" lazy v-model="tabIndex">
                    <b-tab title="DNI" :disabled="tabs.tab1">
                        <!-- <b-alert show>I'm lazy mounted!</b-alert> -->
                        <SearchDNI />
                    </b-tab>
                    <b-tab title="RUC" :disabled="tabs.tab2">
                        <SearchRUC />
                    </b-tab>
                </b-tabs>

            </div>
        </div>
    </div>
</template>

<script>
import SearchDNI from '@/components/Person/SearchDNI'
import SearchRUC from '@/components/Person/SearchRUC'

export default {
    name: 'Consult',
    components: {
        SearchDNI,
        SearchRUC,
    },
    data() {
        return {
            tabIndex: 0,
            selected: 'dni',
            options: [
                { text: 'DNI', value: 'dni' },
                { text: 'RUC', value: 'ruc' },
            ],
            tabs:{
                tab1: false,
                tab2: true
            }
        }
    },
    methods: {

    },
    watch: {
        
        'selected'(){
            if(this.selected == 'dni'){
                this.tabIndex = 0

                this.tabs.tab1 = false
                this.tabs.tab2 = true
            }else{
                this.tabIndex = 1
                this.tabs.tab1 = true
                this.tabs.tab2 = false

            }
        },
        
    },

}
</script>

<style lang="css">

</style>


