<template>
    <div class="container mb-4">
      <div class="row">
        <div class="col text-left">

          <h2 class="text-center">Listado de Beneficiarios ({{ count }})</h2>
          <div class="d-grid gap-2">
            <router-link :to="{ name:'NewBeneficiary' }" custom v-slot="{ navigate }">
              <b-button block class="mx-1" @click="navigate" variant="secondary">
                <b-icon icon="plus-circle" aria-hidden="true"></b-icon> Nuevo Beneficiario
              </b-button>
            </router-link>

          </div>        
          <Search @messageFromChild="changeSearch"/>
          <hr>

          <pagination :rows="total_pages" @messageFromChild="changePage"/>
          <div class="text-center">
            <Modal />
            <div v-if="msg">
              <h2 class="text-danger">{{ msg }}</h2>
            </div>

          </div>


          <div class="col-md-12">
            <b-table striped hover :items="beneficiarys" :fields="fields">
                <template #cell(index)="data">
                  {{ data.index + 1 }}
                </template>

              
                <template v-slot:cell(action)="data">
                  <router-link :to="{ name:'EditBook', params: {bookId: data.item.id} }" custom v-slot="{ navigate }">
                    <b-button @click="navigate" size="sm" class="mx-1" variant="warning">Editar</b-button>
                  </router-link>

                  <router-link :to="{ name:'DeleteBook', params: {bookId: data.item.id} }" custom v-slot="{ navigate }">
                    <b-button @click="navigate" size="sm" class="mx-1" variant="danger">Eliminar</b-button>
                  </router-link>
                  
                </template>


            </b-table>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
import axios from 'axios'
import Pagination from '@/components/Beneficiary/Pagination'
import Modal from '@/components/Beneficiary/Modal'
import Search from '@/components/Beneficiary/Search'

export default {
    name: 'Listbeneficiary',
    components: {
      Search,
      Pagination,
      Modal,
    },
    data() {
        return {
            fields: [
                'index',
                { key: 'dni', label: 'DNI' },
                { key: 'names', label: 'Nombres' },
                { key: 'origin', label: 'Lugar' },
                { key: 'action', label: 'Accion' },
            ],
            count: 0,
            total_pages: 0,
            next: '',
            previous: '',
            beneficiarys: [],
            page: 1,
            search: ' ',
            rpta: {},
            msg: ''
        };
    },
    created() {
        // this.getBeneficiarios()
    },
    methods: {
      changeSearch(search){
        this.search = search
        this.page = 1
        this.getBeneficiarios()
      },
      changePage(page){
        this.page = page
        this.getBeneficiarios()
      },
      getBeneficiarios(page=this.page, search=this.search) {
        
        const path = `${process.env.VUE_APP_BASE_URL}beneficiario/${search}/search/?page=${page}`
        axios.get(path)
        .then(res => {
          this.rpta = res.data
          this.loadData()
          if (this.rpta.message){
            this.msg = this.rpta.message

          }
        })
        .catch(err => {
          console.error(err); 
        })
      },
      loadData(){
        this.count = this.rpta.count
        this.next = this.rpta.next
        this.previous = this.rpta.previous
        this.total_pages = this.rpta.total_pages
        this.beneficiarys = this.rpta.results
        // 
        this.msg = ''

      }
    },
};
</script>

<style lang="css" scoped>

</style>


