<template>
    <div class="container mb-4">
      <div class="row">
        <div class="col text-left">

          <h2 class="text-center">Nuevo Beneficiario</h2>
          <div class="row">
            <!-- <div class="col-offset-3"></div> -->
            <div class="col-md-12">
              <form @submit="onSubmit">
                <div class="form-group">
                  <label for="dni">DNI:</label>
                  <input type="text" id="dni" class="form-control" placeholder="DNI" v-model="form.dni"
                  :class=" {'is-invalid': errores.dni } ">
                  <small :v-if="errores.dni" class="text-danger" v-for="error in errores.dni" :key="error.id">* {{error}}</small>
                </div>
                <div class="form-group">
                  <label for="names">Nombres:</label>
                  <input type="text" id="names" class="form-control" placeholder="Nombres" v-model="form.names"
                  :class=" {'is-invalid': errores.names } ">
                  <small :v-if="errores.names" class="text-danger" v-for="error in errores.names" :key="error.id">* {{error}}</small>
                </div>
                <div class="form-group">
                  <label for="comunidades">Comunidad:</label>
                  
                  <select class="form-select" v-model="form.origin" id="comunidades" :class=" {'is-invalid': errores.origin } ">
                    <!-- <option selected>Choose...</option> -->
                    <option value="null" selected>-- Seleccione comunidad --</option>
                    <option v-for="origin in origins" :key="origin.id" :value="origin.id">{{ origin.name }}</option>
                  </select>
                  
                  <small :v-if="errores.origin" class="text-danger" v-for="error in errores.origin" :key="error.id">* {{error}}</small>

                </div>
                <div class="form-group mt-2">
                  <div class="text-center">
                    <button class="btn btn-success mx-1">
                      <b-icon icon="cloud-upload" aria-hidden="true"></b-icon> Guardar
                    </button>

                    <router-link :to="{ name:'beneficiary' }" custom v-slot="{ navigate }">
                      <a :href="href" @click="navigate" class="btn btn-secondary">
                        <b-icon icon="arrow-return-left" aria-hidden="true"></b-icon> Cancelar
                      </a>
                      
                    </router-link>

                  </div>
                </div>
              </form>
            </div>

          </div>
        </div>
      </div>
  </div>
</template>

<script>
import axios from 'axios'
import swal from 'sweetalert'

axios.defaults.xsrfHeaderName = "X-CSRFTOKEN"
axios.defaults.xsrfCookieName = "csrftoken"
export default {
    name: 'NewBeneficiary',
    components: {
    },
    data() {
        return {
            origins: [],
            errores: {},
            form: {
              dni: '',
              names: '',
              origin: null,
            }
        };
    },
    created() {
      this.getOrigins()
    },
    methods: {
      
      getOrigins(){
        const path = `${process.env.VUE_APP_BASE_URL}origin/`
        axios.get(path)
        .then(res => {
          // console.log(res.data)
          this.origins = res.data
        })
        .catch(err => {
          console.error(err); 
        })
      },
      onSubmit(evt){
        evt.preventDefault()
        // alert("Enviado")
        const path = `${process.env.VUE_APP_BASE_URL}beneficiario/`
        axios.post(path, this.form)
        .then(res => {
          console.log(res)
          console.log(res.data)
          swal("Guardado correctamente", "", "success")
          this.resetForm()
        })
        .catch(err => {
          console.error(err); 
          // console.error(err.response)
          // console.error(err.response.data)
          this.errores = err.response.data
        })
      },
      validations(){

      },
      resetForm(){
        this.form.dni = ''
        this.form.names = ''
        this.form.origin = null
        this.errores = {}
      }
    },
};
</script>

<style lang="css" scoped>

</style>


